/** Media Queries **/
.header {
  position: relative;
}

.contenidog-header {
  position: absolute;
}
.contenidog-header .contenido-header {
  z-index: 10;
  position: relative;
}

.contenidog-header {
  height: 100%;
  width: 100%;
}

.contenido-header {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
@media (min-width: 1024px) {
  .contenido-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-items: center;
  }
}
.contenido-header .header-text {
  max-width: 600px;
  text-align: center;
  padding: 0 1rem;
}
@media (min-width: 1024px) {
  .contenido-header .header-text {
    text-align: left;
  }
}
.contenido-header .header-text h2 {
  line-height: 5rem;
  margin: 0;
  color: white;
  font-size: 3rem;
  font-weight: 900;
  white-space: normal;
}
.contenido-header .header-text h2 span:nth-child(4) {
  font-size: 3.6rem;
}
@media (min-width: 440px) {
  .contenido-header .header-text h2 span:nth-child(4) {
    font-size: 6.1rem;
  }
}
@media (min-width: 1024px) {
  .contenido-header .header-text h2 span:nth-child(4) {
    font-size: 9rem;
  }
}
.contenido-header .header-text h2 span:nth-child(3) {
  color: rgb(56, 196, 243);
  animation: parpadeoNombre 2s linear infinite;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .contenido-header .header-text h2 span:nth-child(3) {
    font-size: 5rem;
  }
}
@media (min-width: 1024px) {
  .contenido-header .header-text h2 span:nth-child(3) {
    font-size: 9rem;
  }
}
@media (min-width: 768px) {
  .contenido-header .header-text h2 {
    font-size: 5rem;
    line-height: 7rem;
  }
}
@media (min-width: 1024px) {
