.contenido-navegacion {
  position: fixed;
  width: 100%;
  height: 100%;
  /*NAVEGACION*/
  /*TABLET NAVEGACION*/
}
@media (min-width: 1024px) {
  .contenido-navegacion {
    position: absolute;
  }
}
.contenido-navegacion ul {
  list-style: none;
  padding: 0;
  width: 100%;
  text-align: center;
}
.contenido-navegacion ul li {
  padding: 0.7rem 0;
}
.contenido-navegacion ul li:hover {
  border-left: 3px solid #3232f6;
  transition: border 0.5s;
}
.contenido-navegacion .navegacion {
  transition: all 0.5s ease-in-out;
  position: relative;
  background-color: rgba(28, 28, 28, 0.5294117647);
  visibility: hidden;
  max-width: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  z-index: 100;
}
.contenido-navegacion .navegacion a {
  color: white;
  font-weight: 600;
  padding: 1rem 0;
  letter-spacing: 0.5px;
  font-size: 20px;
}
.contenido-navegacion .navegacion.visible {
  visibility: visible;
  opacity: 1;
  max-width: 100%;
  z-index: 10000;
  transition: all 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .contenido-navegacion .navegacion.visible {
    max-width: 300px;
  }
}

