@keyframes aumentarMoverImg {
  0% {
    transform: scale(1);
  }
  10%, 20% {
    transform: scale(1.1);
    transform: translateX(50%) translateY(-5%);
  }
  40%, 50% {
    transform: scale(1.15);
    transform: translateX(-20%) translateY(-10%);
  }
  to {
    transform: scale(2);
    transform: translateX(0%) translateY(0%);
  }
}
