  }
}

.circle2 {
  display: none;
  width: 130px;
  top: 25%;
  right: 40%;
  height: 130px;
  animation: latir 12s linear infinite;
  background: rgb(52, 106, 0);
  background: linear-gradient(166deg, rgb(52, 106, 0) 0%, rgb(9, 121, 76) 35%, rgb(3, 112, 134) 100%);
